import React, { useState, useEffect } from 'react';
import { Card, CardBody, CardHeader, Col, Row } from 'reactstrap';
import PROFILE_PICTURE from '../../assets/img/profile-image.png';
import { getScholarships } from '../../handlers/Api/Calls';
import ReactTable from 'react-table-v6';
import { ErrorToast } from '../../components';
import { Link } from 'react-router-dom';
import { ScholarshipActions } from './components/ScholarshipActions';
import './scholarships.scss';

const Scholarships = props => {
  const [data, setData] = useState([]);
  const [error, setError] = useState(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    getScholarships((apiLoading, apiData, apiError) => {
      setData(apiData);
      setError(apiError);
      setIsLoading(apiLoading);
    });
  }, []);
  
  const handleAddScholarship = (newScholarship) => {    
    setData(prevScholarships => [newScholarship, ...prevScholarships]);
  };
  
  const handleEditScholarship = (editedScholarship) => {    
    const edit = (arr, item) => {
      return arr.map(scholarship => {        
        if(scholarship.id === item.id) {
          scholarship = item;
        }
        return scholarship;
      });
    }
    setData(prevScholarships => edit(prevScholarships, editedScholarship))
  }
  
  const handleDeleteScholarship = (scholarshipID) => {
    setData(prevScholarships => prevScholarships.filter(scholarship => {
      return scholarship.id !== scholarshipID;
    }))
  }

  return (
    <div className="animated fadeIn">
      <Row>
        <Col xl={12}>
          {error && <ErrorToast error={error} />}
          <Card>
            <CardHeader>
              <i className="fa fa-align-justify"></i> Scholarships <small className="text-muted">iEducate</small>
            </CardHeader>
            <CardBody>
              <div className="mb-2">
                <ScholarshipActions 
                  showAdd
                  addCallback={handleAddScholarship}
                />
              </div>

              <ReactTable
                data={data}
                loading={isLoading}
                defaultPageSize={10}
                className="-striped -highlight"
                filterable
                defaultFilterMethod={(filter, row) =>
                  !!String(row[filter.id]) && String(row[filter.id]).toLowerCase().includes(filter.value.toLowerCase())}
                columns={[
                  {
                    Header: "Basic",
                    columns: [
                      {
                        Header: "image",
                        accessor: "image",
                        width: 75,
                        Cell: row => <Link to={`/scholarships/${row.original.id}`}>
                          <img 
                            src={row.value && !row.value.includes('fakepath') ? `data:image/png;base64,${row.value}` : PROFILE_PICTURE} 
                            width={40} 
                            height={40} 
                            style={{ borderRadius: 100 }}
                            alt="scholarship" 
                            className="d-block mx-auto"
                          />
                        </Link>,
                        filterable: false
                      },
                      {
                        Header: "title",
                        accessor: "title",
                        Cell: row => <Link to={`/scholarships/${row.original.id}`}>{row.value}</Link>,
                      },
                      {
                        Header: "actions",
                        accessor: "id",
                        filterable: false,
                        width: 150,
                        Cell: ({ original }) => (
                          <ScholarshipActions 
                            showDelete 
                            showEdit 
                            scholarship={original} 
                            deleteCallback={handleDeleteScholarship}
                            editCallback={handleEditScholarship}
                          />
                        )
                      },
                    ]
                  }
                ]}
              />
            </CardBody>
          </Card>
        </Col>
      </Row>
    </div>
  )
}

export default Scholarships;
