import React, { useState } from 'react';
import { Form, Field } from 'react-final-form';
import { RenderInput, RenderCKeditor, RenderFile } from '../../../components';

export const ScholarshipForm = ({
    scholarship = {},
    onSubmit = values => console.log(values),
    isAdd = false,
    children
}) => {
    const [image, setImage] = useState(null);

    const validate = (fields) => {
        const errors = {};
        if(!fields["image"]) errors["image"] = 'required';
        return errors;
    }

    return (
        <div className="scholarship-form">
            <Form
                onSubmit={onSubmit}
                validate={validate}
                initialValues={scholarship}
                render={({ handleSubmit }) => (
                    <form onSubmit={handleSubmit}>
                        <Field 
                            name="title" 
                            label="Scholarship Title"
                            component={RenderInput}
                            placeholder="Title" 
                        />
                        <Field 
                            name="description" 
                            label="Description"
                            component={RenderCKeditor}
                            placeholder="Description" 
                        />
                        <Field 
                            name="image" 
                            label="Image"
                            value={image}
                            component={RenderFile}
                            callback={setImage}
                            placeholder="Image" 
                        />
                        {children}
                    </form>
                )}
            />
        </div>
    )
}
