import React, { useState, useEffect } from 'react';
import { ButtonGroup, Button, Modal, ModalBody, ModalFooter, ModalHeader } from 'reactstrap';
import { ScholarshipForm } from './ScholarshipForm';
import { editScholarship, deleteScholarship, addScholarship } from '../../../handlers/Api/Calls';
import { ErrorToast, FloatToast } from '../../../components';
import { LoadingButton } from '../../../components/LoadingButton';

export const ScholarshipActions = ({
    scholarship = {},
    showAdd = false,
    showEdit = false,
    showDelete = false,
    editCallback,
    deleteCallback,
    addCallback
}) => {
    return (
        <ButtonGroup className="scholarship-actions">
            {showAdd && <AddModal buttonLabel="Add New Scholarship" addCallback={addCallback} />}
            {showEdit && <EditModal buttonLabel="Edit" scholarship={scholarship} editCallback={editCallback} />}
            {showDelete && <DeleteModal buttonLabel="Delete" scholarship={scholarship} deleteCallback={deleteCallback} />}
        </ButtonGroup>
    )
}

const AddModal = ({
    buttonLabel,
    className,
    addCallback = v => console.log(v)
}) => {
    const [modal, setModal] = useState(false);

    const toggle = () => setModal(!modal);

    const [isLoading, setIsLoading] = useState(false);
    const [isSuccess, setIsSuccess] = useState(false);
    const [data, setData] = useState(null);
    const [error, setError] = useState(null);

    const handleAdd = (values) => {
        addScholarship(values, (apiLoading, apiData, apiError, apiSuccess) => {                        
            setIsLoading(apiLoading);
            setIsSuccess(apiSuccess);
            setData(apiData);
            setError(apiError);
        });
    }

    useEffect(() => {
        if (isSuccess && data) {            
            setModal(modal => !modal);
            addCallback(data);
        }
    }, [isSuccess, data]);

    return (
        <>
            <Button color="primary" onClick={toggle}>{buttonLabel}</Button>
            {isSuccess && <FloatToast message="Data added successfuly" />}
            <Modal isOpen={modal} toggle={toggle} className={className}>
                <ModalHeader toggle={toggle}>Add New Scholarship</ModalHeader>
                <ModalBody>
                    <ScholarshipForm isAdd onSubmit={handleAdd}>
                        {error && <ErrorToast error={error} />}
                        <ModalFooter>
                            <LoadingButton loading={isLoading} color="primary" type="submit">Add</LoadingButton>{' '}
                            <Button onClick={toggle}>Cancel</Button>
                        </ModalFooter>
                    </ScholarshipForm>
                </ModalBody>
            </Modal>
        </>
    );
}

const EditModal = ({
    scholarship,
    buttonLabel,
    className,
    editCallback = v => console.log(v)
}) => {
    const [modal, setModal] = useState(false);

    const toggle = () => setModal(!modal);

    const [isLoading, setIsLoading] = useState(false);
    const [isSuccess, setIsSuccess] = useState(false);
    const [data, setData] = useState(null);
    const [error, setError] = useState(null);

    const handleEdit = (values) => {
        editScholarship(values, (apiLoading, apiData, apiError, apiSuccess) => {
            setIsLoading(apiLoading);
            setIsSuccess(apiSuccess);
            setData(apiData);
            setError(apiError);
        });
    }

    useEffect(() => {
        if (isSuccess && data) {
            setModal(modal => !modal);
            editCallback(data);
        }
    }, [isSuccess, data]);

    return (
        <>
            <Button onClick={toggle}>{buttonLabel}</Button>
            {isSuccess && <FloatToast message="Data edited successfuly" />}
            <Modal isOpen={modal} toggle={toggle} className={className}>
                <ModalHeader toggle={toggle}>Edit {scholarship['email']}</ModalHeader>
                <ModalBody>
                    <ScholarshipForm
                        onSubmit={handleEdit}
                        scholarship={scholarship}
                    >
                        {error && <ErrorToast error={error} />}
                        <ModalFooter>
                            <LoadingButton loading={isLoading} color="primary" type="submit">Edit</LoadingButton>{' '}
                            <Button onClick={toggle}>Cancel</Button>
                        </ModalFooter>
                    </ScholarshipForm>
                </ModalBody>
            </Modal>
        </>
    );
}

const DeleteModal = ({
    scholarship,
    buttonLabel,
    className,
    deleteCallback = v => console.log(v)
}) => {
    const [modal, setModal] = useState(false);

    const toggle = () => setModal(!modal);

    const [isLoading, setIsLoading] = useState(false);
    const [isSuccess, setIsSuccess] = useState(false);
    // const [data, setData] = useState(null);
    const [error, setError] = useState(null);

    const handleDelete = () => {
        deleteScholarship(scholarship.id, (apiLoading, apiData, apiError, apiSuccess) => {
            setIsLoading(apiLoading);
            setIsSuccess(apiSuccess);
            // setData(apiData);
            setError(apiError);
        });
    }

    useEffect(() => {
        if (isSuccess && scholarship.id) {
            setModal(modal => !modal);            
            deleteCallback(scholarship.id);
        }
    }, [isSuccess]);

    return (
        <>
            <Button color="danger" onClick={toggle}>{buttonLabel}</Button>
            {isSuccess && <FloatToast message="Data deleted successfuly" />}
            <Modal isOpen={modal} toggle={toggle} className={className}>
                <ModalHeader toggle={toggle}>Delete {scholarship['email']}</ModalHeader>
                <ModalBody>
                    {error && <ErrorToast error={error} />}
                    Are you sure you want to delete this scholarship!
                </ModalBody>
                <ModalFooter>
                    <LoadingButton loading={isLoading} color="danger" onClick={handleDelete}>Delete</LoadingButton>{' '}
                    <Button onClick={toggle}>Cancel</Button>
                </ModalFooter>
            </Modal>
        </>
    );
}